@import './scss/variables';
@import './scss/navbar';
@import './scss/landing';
@import './scss/services';
@import './scss/articles';
@import './scss/footer';



* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    font-family: 'Public Sans', sans-serif;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

@media screen and (max-width: 600px) {
    @import './mobile';
}